.foreground-circle {
    fill: none;
    stroke: #3498db;
}
.foreground-circle-without-stroke-color {
    fill: none;
}
.background-circle {
    fill: none;
    stroke: #ccc;
}
.circle-text {
    font-family: Arial, sans-serif;
    color: #aaa;
    font-size: 20px;
}
.circle-container {
    overflow: visible;
}
.point-circle {
    fill: #f0f8ff;
}
.circle-icon.fa {
    font-size: 30px;
    fill: #000;
}
